@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@900&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}
.header-div {
  width: 100%;
  background-color: #1f2641;
  text-transform: uppercase;
  color: white;
}
.header-btns {
  color: white;
}
.header-logotext {
  font-size: 16px;
  font-weight: 900;
}

.body-paragraph-font {
  font-size: 16px;
  text-transform: capitalize;
  max-width: 444px;
  max-height: 200px;
}
.body-box-timer {
  background-color: white;
  width: 100%;
  min-height: 40px;
  margin: 10px;
  text-align: center;
  border-radius: 15px;
}

